import { Component, Input, Output, EventEmitter, forwardRef, Optional, ElementRef, ViewEncapsulation, OnInit } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { RadioGroupComponent } from './radio-group.component';
import { HostBinding } from '@angular/core';

@Component({
    selector: 'farris-radio-button',
    template: `
    <label [class]="'btn btn-'+type" [class.active]="isGroupModel()" [class.disabled]="disabled">
        <input class="custom-control-input" [name]="name" [value]="label"
        [ngModel]="model" (ngModelChange)="changeModelHandler()" type="radio" [disabled]="disabled">
          <ng-content>
          </ng-content>
    </label>
  `,
    styles: [
        `
        .farris-radio-button>.btn{
            margin-left:-1px
          }
          .farris-radio-button>.btn.active, .farris-radio-button>.btn:hover{
            position: relative;
          }
      `
    ],
    providers: [{
        provide: NG_VALUE_ACCESSOR,
        useExisting: forwardRef(() => RadioButtonComponent),
        multi: true,
    }],
    encapsulation: ViewEncapsulation.None
})
export class RadioButtonComponent implements ControlValueAccessor, OnInit {

    /* radio åçname ç¨äºå¤ä¸ªradioåéä½¿ç¨ */
    // get name() {

    // }
    name = '';

    /* æé®ç±»å */
    @Input()
    type = 'secondary';

    /* radio å¼ */
    @Input()
    get model(): any {
        if (this.hasParent(this.el.nativeElement)) {
            return this.radioGroup.model;
        }
        return this.nyModel;
    }

    set model(model: any) {
        this.nyModel = model;
    }


    @Input()
    id: string;

    /* radio å¼ */
    @Input()
    label: any = '';

    /* ç¦ç¨ */
    @Input()
    disabled: boolean;

    nyModel: any;

    @Output()
    modelChange = new EventEmitter<any>();


    @HostBinding('class.farris-radio-button')
    get farrisRadioButtonClass() { return true; }

    constructor(
        @Optional() public radioGroup: RadioGroupComponent,
        private el: ElementRef
    ) {
    }

    ngOnInit() {
        this.name = this.hasParent(this.el.nativeElement) ? this.radioGroup.name : '';
    }

    isGroupModel() {
        return this.label === this.radioGroup.model;
    }

    changeModelHandler() {
        // ç¶åç´ è·ålabel  å­åç´ åä»ç¶åç´ è·åmodel
        if (this.hasParent(this.el.nativeElement)) {
            return this.radioGroup.changeModel(this.label);
        }
        this.model = this.label;
        this.modelChange.emit(this.model);
        this.controlChange(this.label);
    }

    /* æ¯å¦å­å¨ç¶åç´   å­å¨å³åéç»*/
    hasParent(element: any) {
        return element.parentElement.classList.contains('farris-input-wrap') || element.parentElement.classList.contains('btn-group');
    }

    writeValue(value: any): void {
        this.model = value;
    }

    registerOnChange(fn: Function): void {
        this.controlChange = fn
    }

    registerOnTouched(fn: Function): void {
        this.controlTouch = fn
    }

    private controlChange: Function = () => { }
    private controlTouch: Function = () => { }

}
