import { Component, Input, forwardRef, OnInit, EventEmitter, Optional, Injector } from '@angular/core';
import { NG_VALUE_ACCESSOR, ControlValueAccessor, RadioControlValueAccessor } from '@angular/forms';
import { IdService, CommonUtils } from '@farris/ui-common';
import { Radio } from './radio';
import { Output } from '@angular/core';
@Component({
    selector: 'farris-radiogroup',
    templateUrl: './radiogroup.component.html',
    providers: [{
        provide: NG_VALUE_ACCESSOR,
        useExisting: forwardRef(() => FarrisRadioGroupComponent),
        multi: true,
    }],
    exportAs: 'FarrisRadioGroupComponent'
})
export class FarrisRadioGroupComponent implements ControlValueAccessor, OnInit {
    @Input('tab-index') tabIndex: number;
    /* radio æ°ç» */
    @Input() data: Radio[] = [];

    /* radio ç±»å   åçæèæé®ç±»å*/
    @Input() type: string;

    /* nameæ è¯ */
    @Input() name: string;

    /* æ°´å¹³æå */
    @Input() horizontal: boolean;

    /* ç¦ç¨ */
    @Input() disabled: boolean;

    /* radio å¼ */
    @Input() value: any;
    @Input() textField = 'name';
    @Input() valueField = 'value';
    /* ç¹å»äºä»¶ */
    @Output() changeValue = new EventEmitter<any>();
    private commonUtils: CommonUtils;
    constructor(private idSer: IdService, @Optional() private inject: Injector) {
        if (this.inject) {
            this.commonUtils = this.inject.get(CommonUtils);
        }
    }


    ngOnInit() {
        this.name = this.idSer.generate().replace(/-/g, '_');
    }

    clickHandler(item: any) {
        let val = this._getValue(item);
        if(this.value == val) return;
        // æ´æ¹controlçå¼
        this.value = val;
        this.controlChange(this.value);
        this.controlTouch(this.value);
        this.changeValue.emit(this.value);
    }

    _getText(item) {
        if (item) {
            if (this.commonUtils && this.commonUtils.getValue) {
                return this.commonUtils.getValue(this.textField, item);
            } else {
                return item[this.textField];
            }
        } else {
            return '';
        }
    }

    _getValue(item) {
        if (item) {
            if (this.commonUtils && this.commonUtils.getValue) {
                return this.commonUtils.getValue(this.valueField, item);
            } else {
                return item[this.valueField];
            }
        } else {
            return '';
        }
    }


    writeValue(value: any): void {
        this.value = value;
    }

    registerOnChange(fn: () => void): void {
        this.controlChange = fn;
    }

    registerOnTouched(fn: () => void): void {
        this.controlTouch = fn;
    }

    private controlChange: (value: any) => void = () => { };
    private controlTouch: (value: any) => void = () => { };

}
