import { Component, OnInit, Input, Output, EventEmitter, forwardRef } from '@angular/core';
import { NG_VALUE_ACCESSOR, ControlValueAccessor } from '@angular/forms';
import { IdService } from '@farris/ui-common';
@Component({
    selector: 'farris-checkbox-group',
    template: `
    <div class="farris-input-wrap" [class.farris-checkradio-hor]="horizontal">
      <ng-content></ng-content>
    </div>
  `,
    providers: [{
        provide: NG_VALUE_ACCESSOR,
        useExisting: forwardRef(() => CheckboxGroupComponent),
        multi: true,
    }],
    styles: [],
})
export class CheckboxGroupComponent implements OnInit, ControlValueAccessor {

    /* åå§å checkbox çå¼ */
    @Input()
    model = [];

    /* è®¾ç½®é»è®¤nameå¼ */
    @Input()
    name: string;

    /* æ§ä»¶æ°´å¹³æå */
    @Input()
    horizontal: boolean;

    /*  */
    @Output()
    modelChange = new EventEmitter<any>();

    constructor(private idSer: IdService) {
    }

    ngOnInit() {
        this.name = this.idSer.generate();
    }

    changeModel(value: any) {
        const index = this.model.findIndex(ele => {
            return ele === value;
        });
        // å¦æå·²ç»éä¸­  åè®¾ç½®ä¸ºä¸éä¸­
        if (index > -1) {
            this.model.splice(index, 1);
            return this.modelChange.emit(this.model);
        }
        // å¦ææ²¡æéä¸­  è®¾ç½®ä¸ºéä¸­
        this.model.push(value);
        // controlçå¼è®¾ç½®ä¸ºéå·åéçå­ç¬¦ä¸²  ä¾¿äºå­å¨åä¼ é
        this.controlChange(this.model.join());
        this.modelChange.emit(this.model);
    }
    writeValue(value: any): void {
        this.model = value
        this.changeModel(this.model)
    }

    registerOnChange(fn: Function): void {
        this.controlChange = fn
    }

    registerOnTouched(fn: Function): void {
        this.controlTouch = fn
    }

    private controlChange: Function = () => { }
    private controlTouch: Function = () => { }

}
