import { Component, OnInit, Input, Output, EventEmitter, forwardRef, HostBinding } from '@angular/core';
import { NG_VALUE_ACCESSOR, ControlValueAccessor } from '@angular/forms';
import { IdService } from '@farris/ui-common';
@Component({
    selector: 'farris-radio-group',
    template: `
    <div class="farris-radio-group btn-group"
    [class.farris-input-wrap]="type!=='button'"
    [class.btn-group-toggle]="type==='button'"
    [class.farris-checkradio-hor]="horizontal">
      <ng-content></ng-content>
    </div>
  `,
    providers: [{
        provide: NG_VALUE_ACCESSOR,
        useExisting: forwardRef(() => RadioGroupComponent),
        multi: true,
    }],
    styles: [`
    `],
})
export class RadioGroupComponent implements OnInit, ControlValueAccessor {
    /* radio ç±»å */
    @Input() type: string;

    /* åå§å radio çå¼ */
    @Input() model: any;

    @Input() name: string;

    @Input() horizontal: boolean;

    @Output() modelChange = new EventEmitter<any>();

    // tslint:disable-next-line:ban-types
    subscriber: Function[] = [];

    constructor(private idSer: IdService) {
    }

    ngOnInit() {
        this.name = this.idSer.generate().replace(/-/g, '_');
    }

    changeModel(value: any) {
        this.model = value;
        this.modelChange.emit(this.model);
        this.controlChange(value);
    }

    writeValue(value: any): void {
        this.model = value;
        this.changeModel(this.model);
    }

    registerOnChange(fn: Function): void {
        this.controlChange = fn
    }

    registerOnTouched(fn: Function): void {
        this.controlTouch = fn
    }

    private controlChange: Function = () => { }
    private controlTouch: Function = () => { }

}
