import { Component, Input, forwardRef, OnInit, Output, EventEmitter } from '@angular/core';
import { NG_VALUE_ACCESSOR, ControlValueAccessor } from '@angular/forms';
import { IdService, CommonUtils } from '@farris/ui-common';
import { HttpClient } from '@angular/common/http';
import { Observable, Subscription } from 'rxjs';

@Component({
    selector: 'farris-checkboxgroup',
    templateUrl: './checkboxgroup.component.html',
    providers: [{
        provide: NG_VALUE_ACCESSOR,
        useExisting: forwardRef(() => FarrisCheckboxGroupComponent),
        multi: true,
    }],
    exportAs: 'FarrisCheckboxGroupComponent'
})
export class FarrisCheckboxGroupComponent implements ControlValueAccessor, OnInit, OnDestroy, OnChanges {

    @Input('tab-index') tabIndex = 0;

    /* radio æ°ç» */
    @Input() data: any[] = [];

    /* checkbox ç±»å   åçæèæé®ç±»å*/
    @Input() type: string;

    /* å¤éæ¡name */
    @Input() name: string;

    /* æ¯å¦æ°´å¹³åå¸ */
    @Input() horizontal: boolean;

    /* åéç¬¦ é»è®¤éå·*/
    @Input() separator = ',';

    /* ç¦ç¨ */
    @Input() disable: boolean;

    /* å¤éæ¡ç»çå¼ */
    @Input() value: any;

    /* å¼ç±»åæ¯å¦æ¯string */
    @Input() isStringValue = true;

    @Input() textField = 'name';
    @Input() valueField = 'value';

    /** å è½½æ°æ®äºä»¶ */
    @Input() loadData: (emptyData?: any[]) => Observable<any>;
    loadDataSubscription: Subscription;

    @Output() changeValue = new EventEmitter<any>();

    /* checkboxèµå¼äºä»¶ */
    @Output() setCheckboxData = new EventEmitter<any>();

    private commonUtils: CommonUtils;

    constructor(private idSer: IdService, @Optional() private inject: Injector) {
        if (this.inject) {
            this.commonUtils = this.inject.get(CommonUtils);
        }
    }

    ngOnInit() {
        // åå§åname
        this.name = this.idSer.generate().replace(/-/g, '_');

        this.setCheckboxData.emit(this);
        // todo åå¼å¯ä»¥å®ç°promise
        if (this.loadData) {
            this.loadDataSubscription = this.loadData().subscribe(res => this.data = res.data);
        }
    }

    ngOnChanges(changes: SimpleChanges) {
    }

    ngOnDestroy() {
        if (this.loadDataSubscription) {
            this.loadDataSubscription.unsubscribe();
        }
    }

    _getText(item) {
        if (item) {
            if (this.commonUtils && this.commonUtils.getValue) {
                return this.commonUtils.getValue(this.textField, item);
            } else {
                return item[this.textField];
            }
        } else {
            return '';
        }
    }

    _getValue(item) {
        if (item) {
            if (this.commonUtils && this.commonUtils.getValue) {
                return this.commonUtils.getValue(this.valueField, item);
            } else {
                return item[this.valueField];
            }
        } else {
            return '';
        }
    }

    /* éä¸­inputå¼ */
    clickHandler(item: any, index: number) {
        let arrValue = this.transformToArr(this.value) || [];

        const val = String(this._getValue(item));
        if (!arrValue || !arrValue.length) {
            arrValue.push(val);
        } else {
            if (arrValue.some(item => item == val)) {
                arrValue = arrValue.filter(n => n !== val);
            } else {
                arrValue.push(val);
            }
        }

        // æ´æ°valueå¼
        this.value = this.transformToStr(arrValue);

        // ä¼ å¼formcontrol
        this.controlChange(this.value);
        this.controlTouch(this.value);
        this.changeValue.emit(this.value);
    }

    /* è¢«éä¸­ */
    checked(item: any) {
        const val = String(this._getValue(item));
        const checkedValue = this.transformToArr(this.value);
        // å¤å¼
        return checkedValue.some(item => item == val);
    }

    /* å¼å°æ°ç»å¼çè½¬æ¢ */
    transformToArr(value: any): string[] {
        if (!value) {
            return [];
        }
        if (!this.isStringValue) {
            return value;
        }

        return value.split(this.separator);
    }

    /* å¼å°å­ç¬¦ä¸²å¼çè½¬æ¢ */
    transformToStr(value: Array<string>) {

        const allVals = this.data.map(n => this._getValue(n));
        const r = [];
        allVals.forEach(n => {
            if (value.some(item => item == n)) {
                r.push(n);
            }
        });

        if (!this.isStringValue) {
            return r;
        }
        return r.join(this.separator);
    }


    writeValue(value: any): void {
        this.value = value;
    }

    /* changeæä½å¼åå  æ¹æ³éå*/
    registerOnChange(fn: () => void): void {
        this.controlChange = fn;
    }

    /* blurå¼åå  æ¹æ³éå*/
    registerOnTouched(fn: () => void): void {
        this.controlTouch = fn;
    }

    private controlChange: (value: any) => void = () => { };
    private controlTouch: (value: any) => void = () => { };

}
