import { Component, Input, Output, EventEmitter, forwardRef, Optional, ElementRef, HostBinding, ChangeDetectorRef, OnInit } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { RadioGroupComponent } from './radio-group.component';

@Component({
  selector: 'farris-radio',
  templateUrl: './radio.component.html',
  styleUrls: ['./radio.component.css'],
  providers: [{
    provide: NG_VALUE_ACCESSOR,
    useExisting: forwardRef(() => RadioComponent),
    multi: true,
  }]
  // changeDetection: ChangeDetectionStrategy.OnPush,
})
export class RadioComponent implements ControlValueAccessor, OnInit {

  /* radio åçname ç¨äºå¤ä¸ªradioåéä½¿ç¨ */
  // get name() {
  //   if (this.hasParent(this.el.nativeElement)) {
  //     return this.radioGroup.name;
  //   }
  // }
 
  /* radio å¼ */
  @Input()
  get model(): any {
    if (this.hasParent(this.el.nativeElement)) {
      return this.radioGroup.model;
    }
    return this.nyModel;
  }

  set model(model: any) {
    this.nyModel = model;
  }


  @Input() id: string;

  /* radio å¼ */
  @Input() label: any = '';

  /* ç¦ç¨ */
  @Input() disabled: boolean;

  nyModel: any;

  name = '';

  @Output() modelChange = new EventEmitter<any>();

  @HostBinding('class.d-block') blockClass = true;

  constructor(
    @Optional() private radioGroup: RadioGroupComponent,
    private el: ElementRef
  ) {
  }

  ngOnInit() {
    this.name = this.radioGroup.name;
  }

  changeModelHandler() {
    // ç¶åç´ è·ålabel  å­åç´ åä»ç¶åç´ è·åmodel
    if (this.hasParent(this.el.nativeElement)) {
      return this.radioGroup.changeModel(this.label);
    }
    this.model = this.label;
    this.modelChange.emit(this.model);
    this.controlChange(this.label);
  }

  /* æ¯å¦å­å¨ç¶åç´   å­å¨å³åéç»*/
  hasParent(element: any) {
    return element.parentElement.classList.contains('farris-radio-group');
  }

  writeValue(value: any): void {
    this.model = value;
  }

  registerOnChange(fn: Function): void {
    this.controlChange = fn;
  }

  registerOnTouched(fn: Function): void {
    this.controlTouch = fn;
  }

  private controlChange: Function = () => { };
  private controlTouch: Function = () => { };

}
